.service {
 padding: 0 14%;
  .btn{
    width: 100%;
    height: 63px;
    display: flex;
    a{
      display: flex;
      justify-content: center;
      width: 247px;
      height: 63px;
      border: none;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  .cont {
    display: flex;
    justify-content: space-between;
    padding: 0px 25px;
    background: #efefef;

    img{
      max-width: 260px;
      max-height: 165px;
      margin: 15px 0;
    }
    .servwrap{
      margin: 25px 0;
      h4{
        max-width: 325px;
        font-size: 25px;
        margin-bottom: 15px;
      }
    }
    p {
      max-width: 530px;
    }
  }
}
